import styled from '@emotion/styled'
import { IGatsbyImageData } from 'gatsby-plugin-image'
import {useMemo} from 'react'

import PostItem from '@/components/PostList/PostItem'
import {PostListItemType} from '@/types/PostItem.types'

export interface PostType {
    node: {
        id: string
        frontmatter: {
            title: string
            summary: string
            date: string
            categories: string[]
            thumbnail: {
                childImageSharp: {
                    gatsbyImageData: IGatsbyImageData
                }
            }
        }
    }
}

interface PostListProps {
    selectedCategory : string
    posts: PostListItemType[]
}

const PostListWrapper = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    width: 768px;
    margin: 0 auto;
    padding: 50px 0 100px;
    
    @media (max-width:768px) {
        grid-template-column:1fr;
        width: 100%;
        padding: 50px 20px;
    }
`

const PostList = ({selectedCategory, posts}:PostListProps)=> {
    // ÎßåÏïΩ ÏÑ†ÌÉùÎêú Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä Ï°¥Ïû¨ÌïòÎ©¥ÏÑú All Ïù¥ ÏïÑÎãå Í≤ΩÏö∞ÏóêÎäî Ìï¥Îãπ Ïπ¥ÌÖåÍ≥†Î¶¨ Í∞íÏùÑ Í∞ÄÏßÑ Ìè¨Ïä§Ìä∏ ÏïÑÏù¥ÌÖúÎßå ÌïÑÌÑ∞ÎßÅ ÌïòÎèÑÎ°ù ÌñàÍ≥†, Í∑∏Î†áÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ÏóêÎäî Î™®Îì† Ìè¨Ïä§Ìä∏ ÏïÑÏù¥ÌÖúÏùÑ Î≥¥Ïó¨Ï£ºÎèÑÎ°ù Íµ¨ÌòÑ
    const postListData = useMemo(
        ()=>
            posts.filter(
                ({
                    node: {
                        frontmatter: {categories},
                    },
                }:PostListItemType)=>(selectedCategory!=='All'?categories.includes(selectedCategory):true),
            ),
            [selectedCategory],
    )

    return(
        <PostListWrapper>
            {postListData.map(({node:{id, fields: {slug}, frontmatter,}}:PostListItemType)=>(
                <PostItem {...frontmatter} link={slug} key={id}/>
            ))}
        </PostListWrapper>
    )
}

export default PostList